  }
  
  .uploaded-images {
    margin-top: 20px;
    text-align: center;
  }
  
  .image-preview {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    gap: 10px;
  }
  
  .image-container {
    position: relative;
    border-radius: 5px;
    overflow: hidden;
    flex: 0 0 calc(25% - 50px);
    border: 1px solid green;
  }
  
  .image-container img {
    max-width: 100%;
    max-height: 100%;
    border-radius: 5px;
    border: 1px solid #ddd;
  }
  
  .delete-button {
    position: absolute;
    top: 5px;
    right: 5px;
    background-color: #f44336;
    color: white;
    border: none;
    border-radius: 50%;
    padding: 4px;
    cursor: pointer;
    font-size: 12px;
  }
  
  .delete-button:hover {
    background-color: #d32f2f;
  }

  .image-preview img {
    max-width: 100px;
    max-height: 100px;
    border-radius: 5px;
    border: 1px solid #ddd;
  }
  
  .upload-button {
    margin-top: 10px;
    padding: 8px 16px;
    background-color: #4caf50;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }
  
  .upload-button:hover {
    background-color: #45a049;
  }
    